<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-row :gutter="20" style="width:1100px;">
          <el-col :span="6" style="text-align:left">
            <div class="grid-content bg-purple">
              <img
              @click="logoClick"
                src="//static2.cnodejs.org/public/images/cnodejs_light.svg"
                alt
                style="height:36px;margin-top:10px"
                class="logo"
              />
            </div>
          </el-col>
          <el-col :span="14">
            <div class="grid-content bg-purple">
              <el-menu
                :default-active="activeIndex"
                class="el-menu-demo"
                mode="horizontal"
                text-color="#fff"
                active-text-color="#ffd04b"
                background-color="#545c64"
                @select="handleSelect"
                router
              >
                <el-menu-item
                  v-for="(item,index) in topics"
                  :key="index"
                  :index="item.name"
                >{{item.desc}}</el-menu-item>
              </el-menu>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content bg-purple" style="line-height:60px">
              <span
                @click="dialogVisible = true"
                style="line-height:62px;color:white"
                :class="isLogined?'hide':''"
              >登录</span>
              <el-dropdown :class="isLogined?'':'hide'" @command="handleClickMoreMenus">
                
                  <span class="el-dropdown-link" style="color:white">
                    
                  {{$store.state.loginname}}
                  <i class="el-icon-arrow-down el-icon--right"></i>
                  <el-badge :value="badgeText" v-if="badgeText" class="items">
                </el-badge>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="center">
                    <i class="el-icon-user-solid"></i>
                   个人中心
                    </el-dropdown-item>
                  <el-dropdown-item command="msg">
                    <i class="el-icon-message-solid"></i>
                    消息通知<el-badge is-dot class="items" v-if="badgeText"></el-badge>
                    </el-dropdown-item>
                  <el-dropdown-item command="create">
                    <i class="el-icon-s-comment"></i>发表话题</el-dropdown-item>
                  <el-dropdown-item command="logout">
                    <!-- <svg t="1567946952306" class="icon" viewBox="0 0 1024 1024" version="1.1" width="15" height="15"><path d="M463.121779 125.44199c0-33.995255 29.188786-61.538565 65.182558-61.538565 35.999912 0 65.172325 27.54331 65.172325 61.538565l0 342.323553c0 33.985022-29.172413 61.533448-65.172325 61.533448-35.994796 0-65.182558-27.548426-65.182558-61.533448L463.121779 125.44199 463.121779 125.44199zM512.005117 960.095551c-224.976938 0-407.359778-172.21527-407.359778-384.640334 0-163.541727 108.297518-302.880215 260.71079-358.520443L365.356129 353.862353c-78.180594 46.232974-130.354883 128.068817-130.354883 221.592864 0 144.455019 124.027781 261.551948 277.004895 261.551948 152.98837 0 276.999778-117.096928 276.999778-261.551948 0-79.858816-38.009685-151.2334-97.760534-199.215204L691.245384 230.454696c134.977157 62.657039 228.110301 193.397708 228.110301 345.011778C919.364895 787.880281 736.982054 960.095551 512.005117 960.095551L512.005117 960.095551zM512.005117 960.095551" p-id="4814"></path></svg> -->
                    <i class="el-icon-switch-button"></i>退出</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
    <el-dialog title="登录" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <el-input v-model="accesstoken" placeholder="Access Token" style="margin-bottom:20px"></el-input>
      <el-button type="success" @click="doLogin">登录</el-button>
    </el-dialog>
  </div>
</template>

<script>

import { mapState, mapMutations } from "vuex";
export default {
  name: "App",
  computed: {
    ...mapState(["topics", "isLogined"])
  },
  data() {
    return {
      activeIndex: "all",
      dialogVisible: false,
      accesstoken: "",
      badgeText: '',
    };
  },
  methods: {
    ...mapMutations([
      "mutationLogin",
      "mutationLoginname",
      "mutationAccessToken",
      "mutationAvatarUrl",
      "mutationAuthorId",
      "mutationScore"
    ]),
    // 获取服务器端未读消息个数
    getUnreadMsgCount: function() {
      var app = this;
      this.$http.get('/message/count', {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      }).then(function(response) {
        // 修改当前badgeText值
        if (response.data.success) {
          app.badgeText = response.data.data;
        }
      });
    },
    handleSelect(key, keyPath) {
      console.log(key);
      this.activeIndex=key;
      this.$router.push("/topiclist/" + key);
    },
    handleClickMoreMenus(command) {
      this.activeIndex=""
      this.$router.push("/" + command);
    },
    handleClose(done) {
      this.$confirm("确认退出登录吗？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    doLogin() {
      // debugger
      var app = this;
      this.$http
        .post("/accesstoken", {
          accesstoken: this.accesstoken
        })
        .then(res => {
          if (res.data && res.data.success) {
            console.log(res.data);
            app.mutationLogin();
            app.mutationLoginname(res.data.loginname);
            app.mutationAccessToken(app.accesstoken);
            app.mutationAuthorId(res.data.id);
            app.dialogVisible = false;
            window.localStorage.setItem("loginStatus", true);
            window.localStorage.setItem("loginname", res.data.loginname);
            window.localStorage.setItem("accesstoken", app.accesstoken);
            window.localStorage.setItem("avatar_url", res.data.avatar_url);
            window.localStorage.setItem("author_id", res.data.id);
            app.$router.push("/center");
            app.getUnreadMsgCount();
            app.accesstoken="";
            app.activeIndex="";
          }
          console.log(res);
        });
    },
    logoClick(){
     // debugger
      this.$router.push("/");
      this.activeIndex="all"
    }
  },
  created() {
     // debugger
    var loginStatus = window.localStorage.getItem("loginStatus");
    if (loginStatus) {
      this.mutationLogin();
      this.mutationLoginname(window.localStorage.getItem("loginname"));
      this.mutationAccessToken(window.localStorage.getItem("accesstoken"));
      this.mutationAvatarUrl(window.localStorage.getItem("avatar_url"));
      this.mutationAuthorId(window.localStorage.getItem("author_id"));
      this.mutationScore(window.localStorage.getItem("score"))
    }

   if(this.isLogined){
    // ajax获取未读消息个数
    this.getUnreadMsgCount();
    // 采用定时操作来实现该业务
    var app = this;
    window.setInterval(function() {
      app.getUnreadMsgCount();
    }, 5*60*1000);
   }
    
  },
  
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */
}
* {
  margin: 0;
  padding: 0;
}

.markdown-text img{
    width: 100% ;
}
.logo{
  cursor: pointer;
}
ul li {
  list-style: none;
}
.el-row {
  margin-bottom: 20px;
  margin: 0 auto !important;
}
.el-row:last-child {
  margin-bottom: 0;
}

.el-menu {
  top: -1px;
  background: #545c64;
}

.el-header {
  background: #545c64;
}
.el-header,
.el-footer .el-main {
  color: #333;
  text-align: center;
  line-height: 60px;
  border-bottom: 1px solid #e9e9e9;
}
.el-main {
  width: 1100px;
  margin: 0 auto;
  padding-left: 0;
  padding-right: 0;
}
body > .el-container {
  margin-bottom: 40px;
}
.hide {
  display: none;
}
.box-card .el-card__header {
  padding: 8px 20px;
}
.el-dialog__header {
  border-bottom: 1px solid #e9e9e9;
}
.items {
  /* margin-top: 10px; */
}

.detail_info table, th,td {
    border: 1px solid #e9e9e9;
    border-collapse: collapse;
    text-align: left;
    margin: 0;
    padding: 6px 13px;
}
.detail_info table{
      padding: 0;
    border-collapse: collapse;
    border-spacing: 0;
    font: inherit;
}
.detail_info table tr:nth-child(even){
  background: #f8f8f8;
}
.detail_info h1 ,.detail_info h2, .detail_info h3,.detail_info h4{
  border-bottom:1px solid #ccc;
  margin: 20px 0px;
} 
.detail_info h1{
  font-size: 2em;
}
.detail_info h2{
  font-size: 1.5em;
}
.detail_info h3{
  font-size: 1.3em;
}
.detail_info .markdown-text p, .preview p {
    white-space: pre-wrap;
    word-wrap: break-word;
    line-height: 2em;
    margin: 1em 0;
}
.w-e-toolbar{
  border: none !important;
  background-color: transparent !important;
      border-bottom: 1px solid #e9e9e9 !important;
    border-top: 1px solid #e9e9e9 !important;
}
.w-e-toolbar{
  padding: 12px 5px;
}
.w-e-text-container{
  z-index: 0 !important;
}
.w-e-text-container{
  border: none !important;
  border-bottom: 1px solid #e9e9e9 !important;
}
.w-e-text{
  overflow: hidden;
}
.el-popper{
  z-index: 20001 !important;
}
</style>



