<template class="page-component__scroll">
  <div v-if="topic">
       <el-backtop :bottom="60"></el-backtop>
    <el-row :gutter="20">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>个人信息</span>
            </div>
            <div class="text item">
              <div>
                <el-avatar shape="square" :size="50" :src="topic?topic.author.avatar_url:''"></el-avatar>
                <span
                  style="position:relative;top:-20px;left:5px;font-size:18px"
                >{{topic?topic.author.loginname:''}}</span>
              </div>
              <p>积分：90</p>
            </div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="18" style="padding-right:0">
        <div class="grid-content bg-purple">
          <div style="border-bottom:1px solid #e9e9e9;">
            <span class="top_tab" v-if="topic.good">精华</span>
            <h3 style="display:inline">{{topic.title}}</h3>
            <ul class="ul">
              <li>发布于{{topic.create_at}}</li>
              <li>
                作者
                <el-link :href="'/#/user/'+topic.author.loginname" style="line-height: 15px;">{{topic.author.loginname}}</el-link>
              </li>
              <li>{{topic.visit_count}} 次浏览</li>
              <li>来自 {{getDescByTab(topic.tab)}}</li>
              <li style="float:right;list-style:none">
                  <el-button type="success" size="mini" v-if="topic.is_collect==false" @click="collect">收藏</el-button>
                 <el-button type="info" size="mini" v-else @click="decollect">取消收藏</el-button>
              </li>
            </ul>
            <span v-if="topic.author_id==$store.state.author_id">
                <i class="el-icon-edit" style="margin:0px 10px" @click="editClick(topic.id)"></i>
                <i class="el-icon-delete"></i>
            </span>
            
          </div>
          <p v-html="topic.content" style="padding:20px" class="detail_info"></p>
        </div>
        <div>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{topic.replies.length}}回复</span>
            </div>
            <div v-for="(item,index) in topic.replies" :key="index" class="text item">
              <el-avatar
                shape="square"
                size="small"
                :src="item.author.avatar_url"
                style="float:left;margin-right:20px"
              ></el-avatar>
              <el-link href="#" style="float:left;margin-right:20px">{{item.author.loginname}}</el-link>
              <span>{{index+1}}楼</span>
              <span v-html="item.content" style="padding:10px" class="comment"></span>
            </div>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  name: "topic",
  props: ["id"],
  data() {
    return {
      topic: null
    };
  },
  methods:{
      ...mapGetters(["getTopicDescByTab"]),
      getDescByTab(tab) {
      return this.$store.getters.getTopicDescByTab(tab);
    },
    collect(){
        var app = this;
      this.$http.post('/topic_collect/collect', {
        accesstoken: this.$store.state.accesstoken,
        topic_id: this.topic.id
      }).then(function(response) {
        // 判断是否成功
        if (response.data && response.data.success) {
          // ajax修改按钮状态 “收藏” -> “取消收藏”
          app.topic.is_collect = true;
        }
      });
    },
    // 取消收藏主题
    decollect() {
      var app = this;
      this.$http.post('/topic_collect/de_collect', {
        accesstoken: this.$store.state.accesstoken,
        topic_id: this.topic.id
      }).then(function(response) {
        if (response.data && response.data.success) {
          app.topic.is_collect = false;
        }
      });
    },
    editClick(id){
        this.$router.push("/topic/"+id+"/edit")
    }
  },
  beforeRouteEnter: function(to, from, next) {
    //   debugger
    next(function(vm) {
      // 执行Ajax
      vm.$http
        .get("/topic/" + to.params.id, {
          params: {
            accesstoken: vm.$store.state.accesstoken
          }
        })
        .then(function(response) {
          console.log(response.data);
          vm.topic = response.data.data;
        });
    });
  },
  //监听数据变化
  beforeRouteUpdate: function(to, from, next) {
    //   debugger
    var app = this;
    this.$http
      .get("/topic/" + to.params.id, {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(response) {
        console.log(response.data);
        app.topic = response.data.data;
      });
  }
};
</script>
<style scoped>
.grid-content {
  border-radius: 4px;
  min-height: 36px;
  line-height: 36px;
}
.ul {
  overflow: hidden;
}
.ul li {
  list-style-type: disc;
  float: left;
  font-size: 12px;
  margin-left: 16px;
  margin-right: 25px;
}

.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
  border-bottom: 1px solid #e9e9e9;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.titles{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
    width: 70%;
    display:inline-block;
}
.comment{
    display: block;
}
.top_tab{
  font-size: 14px;
  padding: 2px 5px;
  border-radius: 4px;
}
.top_tab {
  color: white;
  background: #80bd01;
}

</style>


