<template>
  <div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="未读消息" name="first">
          <el-card class="box-card" shadow="hover">
          <ul v-if="hasNotReadMsgs.length>0">
            <li>
              <el-button type="text" @click="markAll">全部标记已读</el-button>
            </li>
              <li v-for="(item, index) in hasNotReadMsgs" :key="index" class="lists_li">
                  <el-link type="primary" :href="'/#/user/' + item.author.loginname">{{item.author.loginname}}</el-link>
                  回复了你的话题
                  <el-link type="primary" :href="'/#/topic/' + item.topic.id" @clicl="markRead(item.id)">{{item.topic.title}}</el-link>
              </li>
          </ul>
          <div v-else>
             <i class="el-icon-info"></i>暂无消息
          </div>
          </el-card>
      </el-tab-pane>
      <el-tab-pane label="已读消息" name="second">
          <el-card class="box-card" shadow="hover">
          <ul>
              <li v-for="(item, index) in hasReadMsgs" :key="index" class="lists_li">
                  <el-link type="primary" :href="'/#/user/' + item.author.loginname">{{item.author.loginname}}</el-link>
                  回复了你的话题
                  <el-link type="primary" :href="'/#/topic/' + item.topic.id">{{item.topic.title}}</el-link>
              </li>
          </ul>
          </el-card>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "msg",
  data() {
    return {
      activeName: "first",
      hasReadMsgs: null,
      hasNotReadMsgs: null
    };
  },
  methods: {
    handleClick(tab, event) {
    //   console.log(tab, event);
    },
    // 标记指定消息为已读
    markRead: function(mid) {
      debugger
      // console.log('mid = ' + mid);
      var app = this;
      this.$http.post('/message/mark_one/' + mid, {
        accesstoken: this.$store.state.accesstoken
      }).then(function(response) {
        if (response.data && response.data.success) {
          // 标记为已读操作是完成了
          console.log("已经标记为已读");
          // ajax后续处理
          // 在未读消息列表中删除当前记录
          // 把当前记录添加到已读消息列表中
        }
      });
    },
     // 标记所有消息为已读
    markAll: function() {
      var app = this;
      this.$http.post('/message/mark_all', {
        accesstoken: this.$store.state.accesstoken
      }).then(function(response) {
        if (response.data && response.data.success) {
          // 标记为已读操作是完成了
          console.log("已经全部标记为已读");
          // ajax后续处理
          // 模拟实现路由刷新
          app.$router.go(0);
        }
      });
    },
  },
  created: function() {
    // ajax获取消息数据
    var app = this;
    this.$http
      .get("/messages", {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(response) {
             console.log(response.data);
        if (response.data && response.data.success) {
          app.hasReadMsgs = response.data.data.has_read_messages;
          app.hasNotReadMsgs = response.data.data.hasnot_read_messages;
        }
      });
  }
};
</script>
<style scoped>
.lists_li {
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 10px;
  line-height: 50px;
}
</style>


