<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          <el-card class="box-card">
            <p>
            选择板块：
            <el-select v-model="value" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </p>
          <p style="margin-top:15px">
              <el-input v-model="input" placeholder="标题字数10字以上"></el-input>
          </p>
          <div ref="editor" style="text-align:left;margin:15px 0"></div>
          <div>
              <el-button type="primary" @click="submitClick">提交</el-button>
          </div>
          </el-card>
          
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import E from "wangeditor";
export default {
  name: "create",
  data() {
    return {
        editor: "",   //富文本编辑器
        input:"",
      value: "",
      options: [
          {
          value: "",
          label: "请选择"
        },
        {
          value: "share",
          label: "分享"
        },
        {
          value: "ask",
          label: "问答"
        },
        {
          value: "job",
          label: "招聘"
        },
        {
          value: "dev",
          label: "客户端测试"
        }
      ]
    };
  },
  methods: {
      submitClick(){
          this.$message("此API接口已下线！")
      }
  },
  mounted() {
    //富文本编辑器设置
    this.editor = new E(this.$refs.editor);
    this.editor.customConfig.onchange = html => {
      this.editorContent = html;
    };
    this.editor.create();
  }
};
</script>
<style scoped>
    .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>


