                    p-id="2102"
                  />
                </svg>
                <el-link
                  style="top:-5px"
                  :href="'https://github.com/'+userinfo.githubUsername"
                >@{{userinfo.githubUsername}}</el-link>
              </p>
              <p>注册时间：{{userinfo.create_at}}</p>
            </div>
          </el-card>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>最近创建的话题</span>
            </div>
            <ul>
              <li class="lists_li" v-for="(item,index) in userinfo.recent_topics" :key="index">
                <div class="text item">
                  <el-row :gutter="20">
                    <el-col :span="21">
                      <div class="grid-content bg-purple">
                        <el-avatar
                          style="position:relative;top:7px;margin-right:20px"
                          shape="square"
                          size="small"
                          :src="item.author.avatar_url"
                        ></el-avatar>
                        <el-link
                          type="primary"
                          :href="'/#/topic/' + item.id"
                          class="titles"
                        >{{item.title}}</el-link>
                      </div>
                    </el-col>
                    <el-col :span="3">
                      <div class="grid-content bg-purple">
                        <el-avatar
                          shape="square"
                          :size="20"
                          :src="item.author.avatar_url"
                          style="position:relative;top:14px;"
                        ></el-avatar>
                        <span class="span_time">{{dateFormat(item.last_reply_at)}}</span>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </li>
            </ul>
          </el-card>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>最近参与的话题</span>
            </div>
            <ul>
              <li class="lists_li" v-for="(items,index) in userinfo.recent_replies" :key="index">
                <div class="text item">
                  <el-row :gutter="20">
                    <el-col :span="21">
                      <div class="grid-content bg-purple">
                        <el-avatar
                          style="position:relative;top:7px;margin-right:20px"
                          shape="square"
                          size="small"
                          :src="items.author.avatar_url"
                        ></el-avatar>
                        <el-link type="primary" :href="'/#/topic/' + items.id" class="titles">{{items.title}}</el-link>
                      </div>
                    </el-col>
                    <el-col :span="3">
                      <div class="grid-content bg-purple">
                        <el-avatar
                          shape="square"
                          :size="20"
                          :src="items.author.avatar_url"
                          style="position:relative;top:14px"
                        ></el-avatar>
                        <span class="span_time">{{dateFormat(items.last_reply_at)}}</span>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </li>
            </ul>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>

export default {
  name: "user",
  props: ["loginname"],
  data() {
    return {
      userinfo: "",
      collect_topics: []
    };
  },
  methods: {
    dateFormat(date){
        return this.$moment(date).format("YYYY-MM")
    },
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.$http.get("/user/" + to.params.loginname).then(function(response) {
         console.log(response.data);
        vm.userinfo = response.data.data;
      });
      //获取用户收藏数据
      vm.$http
        .get("/topic_collect/" + to.params.loginname)
        .then(function(response) {
         // console.log(response)
          vm.collect_topics = response.data.data;
        });
    });
  },
  beforeRouteUpdate: function(to, from, next) {
    var app = this;
    this.$http.get("/user/" + to.params.loginname).then(function(response) {
      // console.log(response.data);
      app.userinfo = response.data.data;
    });
    app.$http
      .get("/topic_collect/" + to.params.loginname)
      .then(function(response) {
        app.collect_topics = response.data.data;
      });
    next();
  },
  
};
</script>
<style scoped>
.lists_li {
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 10px;
  /* padding-bottom: 10px; */
}
.lists_li:hover {
  background: #f6f6f6;
}
.box-card {
  margin-bottom: 20px;
}
.item > p {
  margin-top: 10px;
}
.titles {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  width: 70%;
  display: inline-block;
}
.span_time {
  font-size: 14px;
  line-height: 50px;
  float: right;
}
</style>


