// vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

Vue.use(Vuex);

const store=new Vuex.Store({
    state:{
        // 主题分类数据
        topics: [
            { name: 'all', desc: '全部' },
            { name: 'good', desc: '精华' },
            { name: 'share', desc: '分享' },
            { name: 'ask', desc: '问答' },
            { name: 'job', desc: '招聘' },
            { name: 'dev', desc: '客户端测试' },
        ],
        isLogined: false,
        // 登录用户名
        loginname: '',
        // 登录用户的accesstoken
        accesstoken: '',
        //头像
        avatar_url:"",
        author_id: '',
        score:""
    },
    getters:{
        getTopicDescByTab: function(state) {
            // 需要返回一个函数结构
            return function(tab) {
                for (var index = 0; index < state.topics.length; index++) {
                    if (state.topics[index].name == tab) {
                        return state.topics[index].desc;
                    }
                }
            };
        }
    },
    mutations,
    actions
})



// 对外输出模块
export default store;


