const mutations = {
    mutationLogin(state){
        state.isLogined=true
    },
    // 修改loginnmae
    mutationLoginname: function(state, payload) {
        state.loginname = payload;
    },
    // 修改accesstoken
    mutationAccessToken: function(state, payload) {
        state.accesstoken = payload;
    },
    mutationAvatarUrl: function(state, payload) {
        state.avatar_url = payload;
    },
    //退出
    mutationLogout(state,payload){
        state.isLogined=false;
        state.accesstoken="";
        state.loginname="";
        state.avatar_url="";
    },
    // 修改author_id
    mutationAuthorId: function(state, payload) {
        state.author_id = payload;
    },
    mutationScore: function(state, payload) {
        state.score = payload;
    }
};

export default mutations;


