<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{loginname}}收藏的话题</span>
      </div>
      <el-row :gutter="20">
        <el-col :span="24" style="padding-right:0">
          <div class="grid-content bg-purple">
            <ul>
              <li
                class="lists_li"
                v-for="(item,index) in collect_topics.slice((page-1)*pageSize,page*pageSize)"
                :key="index"
              >
                <el-row :gutter="20">
                  <el-col :span="21">
                    <div class="grid-content bg-purple">
                      <el-avatar
                        style="position:relative;top:7px"
                        shape="square"
                        size="small"
                        :src="item.author.avatar_url"
                      ></el-avatar>
                      <span style="font-size:14px;display:inline-block;width:72px">
                        {{item.reply_count}}/
                        <i
                          style="font-size:12px;color:#b4b4b4;"
                        >{{item.visit_count}}</i>
                      </span>
                      <span class="top_tab" v-if="item.top">置顶</span>
                      <span class="top_tab" v-else-if="item.good">精华</span>
                      <span
                        class="tab_title"
                        v-else-if="$route.path == '/topiclist/all'"
                      >{{getDescByTab(item.tab)}}</span>
                      <span class="titles">
                        <a :href="'/#/topic/' + item.id">{{item.title}}</a>
                      </span>
                    </div>
                  </el-col>
                  <el-col :span="3">
                    <div class="grid-content bg-purple">
                      <el-avatar
                        shape="square"
                        :size="20"
                        :src="item.author.avatar_url"
                        style="position:relative;top:14px"
                      ></el-avatar>
                      <span class="span_time">{{dateFormat(item.last_reply_at)}}</span>
                    </div>
                  </el-col>
                </el-row>
              </li>
            </ul>
          </div>
          <el-pagination
            style="float:right"
            :current-page="page"
            :total="collect_topics.length"
            :page-size="pageSize"
            layout="prev, pager,  next"
            @next-click="nextClick"
            @prev-click="prevClick"
            @current-change="currentChange"
          ></el-pagination>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  name: "collect",
  props: ["loginname"],
  data() {
    return {
      collect_topics: [],
      page: 1,
      pageSize: 20
    };
  },
  created() {
    var app = this;
    this.$http
      .get("/topic_collect/" + this.$route.params.loginname)
      .then(function(response) {
        console.log(response);
        app.collect_topics = response.data.data;
      });
  },
  methods: {
    nextClick(pages) {
      console.log(pages);
      this.page = pages;
    },
    prevClick(pages) {
      console.log(pages);
      this.page = pages;
    },
    currentChange(pages){
        console.log(pages);
        this.page = pages;
    },
    dateFormat(date){
        return this.$moment(date).format("YYYY-MM-DD")
    },
  }
};
</script>
<style scoped>
.lists_li {
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 10px;
}
.lists_li:hover {
  background: #f6f6f6;
}
.span_time {
  font-size: 14px;
  line-height: 50px;
  float: right;
}
.top_tab,
.tab_title {
  font-size: 14px;
  padding: 2px 5px;
  border-radius: 4px;
}
.top_tab {
  color: white;
  background: #80bd01;
}

.titles a {
  color: black;
  text-decoration: none;
  white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
    width: 70%;
    display:inline-block;
}
.titles:hover a {
  cursor: pointer;
  text-decoration: underline;
}
</style>


