<template>
  <div class="page-component__scroll">
    <el-backtop :bottom="60"></el-backtop>
    <!-- <el-container>
    <el-main>-->
    <el-row :gutter="20">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <el-card class="box-card" v-if="$store.state.isLogined==false">
            CNode：<span style="font-size:14px">  Node.js专业中文社区</span>
            
            <ul>
              <li style="height:50px">
                <img src="../images/ruby-china-20150529.png" style="height:100%"/>
              </li>
            </ul>
          </el-card>
          <el-card class="box-card" v-else>
            <div slot="header" class="clearfix">
              <span>个人信息</span>
            </div>
            <div class="text item">
              <div>
                <el-avatar shape="square" :size="50" :src="this.$store.state.avatar_url"></el-avatar>
                <span
                  style="position:relative;top:-20px;left:5px;font-size:18px"
                >{{this.$store.state.loginname}}</span>
              </div>
              <p style="margin-top:10px">积分：{{$store.state.score}}</p>
            </div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="18" style="padding-right:0">
        <div class="grid-content bg-purple">
          <ul>
            <li class="lists_li" v-for="(item,index) in dataList" :key="index">
              <el-row :gutter="20">
                <el-col :span="21">
                  <div class="grid-content bg-purple">
                    <el-avatar
                      style="position:relative;top:7px"
                      shape="square"
                      size="small"
                      :src="item.author.avatar_url"
                    ></el-avatar>
                    <span style="font-size:14px;display:inline-block;width:72px">
                      {{item.reply_count}}/
                      <i
                        style="font-size:12px;color:#b4b4b4;"
                      >{{item.visit_count}}</i>
                    </span>
                    <span class="top_tab" v-if="item.top">置顶</span>
                    <span class="top_tab" v-else-if="item.good">精华</span>
                    <span
                      class="tab_title"
                      v-else-if="$route.path == '/topiclist/all'"
                    >{{getDescByTab(item.tab)}}</span>
                    <span class="titles">
                      <a :href="'/#/topic/' + item.id">{{item.title}}</a>
                    </span>
                  </div>
                </el-col>
                <el-col :span="3">
                  <div class="grid-content bg-purple">
                    <el-avatar
                      shape="square"
                      :size="20"
                      :src="item.author.avatar_url"
                      style="position:relative;top:14px"
                    ></el-avatar>
                    <span class="span_time">{{dateFormat(item.last_reply_at)}}</span>
                  </div>
                </el-col>
              </el-row>
            </li>
          </ul>
        </div>
        <el-pagination
          style="float:right"
          :current-page="page"
          layout="prev, next"
          @next-click="nextClick"
          @prev-click="prevClick"
        ></el-pagination>
      </el-col>
    </el-row>
    <!-- </el-main>
    </el-container>-->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "topicList",
  props: ["name"],
  data() {
    return {
      page: 1,
      limit: 20,
      dataList: []
    };
  },
  methods: {
    ...mapGetters(["getTopicDescByTab"]),
    nextClick(pages) {
      console.log(pages);
      this.page = pages;
      this.dataLists();
    },
    prevClick(pages) {
      console.log(pages);
      this.page = pages;
      this.dataLists();
    },
    // 根据tab获取topic.desc
    getDescByTab(tab) {
      return this.$store.getters.getTopicDescByTab(tab);
    },
    dateFormat(date) {
      return this.$moment(date).format("YYYY-MM");
    },
    dataLists() {
      var app = this;
      this.$http
        .get("/topics", {
          params: {
            tab: this.$route.params.name,
            page: this.page,
            limit: this.limit
          }
        })
        .then(res => {
          console.log(res.data);
          if (res.data.success) {
            app.dataList = res.data.data;
          }
        });
    }
  },
  created() {
    this.dataLists();
  },
  watch: {
    $route(to, from) {
      // debugger
      if (this.$route.params.name) {
        this.page = 1;
        var app = this;
        this.$http
          .get("/topics", {
            params: {
              tab: to.params.name,
              page: this.page,
              limit: this.limit
            }
          })
          .then(res => {
            if (res.data.success) {
              app.dataList = res.data.data;
            }
            console.log(res);
          });
      }
    }
  }
};
</script>

<style scoped>
.grid-content {
  border-radius: 4px;
  min-height: 36px;
  /* line-height: 36px; */
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.lists_li {
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 10px;
}
.lists_li:hover {
  background: #f6f6f6;
}
.titles a {
  color: black;
  text-decoration: none;
}
.titles:hover a {
  cursor: pointer;
  text-decoration: underline;
}
.span_time {
  font-size: 14px;
  line-height: 50px;
  float: right;
}
.top_tab,
.tab_title {
  font-size: 14px;
  padding: 2px 5px;
  border-radius: 4px;
}
.top_tab {
  color: white;
  background: #80bd01;
}
.tab_title {
  background: #e9e9e9;
}
.titles {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  width: 70%;
  display: inline-block;
}
</style>


