<template>
    <div>
        logout
    </div>
</template>
<script>
export default {
    name:"logout",
    created: function() {
    // 实现注销操作
    // 1. 恢复vuex中数据为原始状态
    this.$store.commit('mutationLogout');
    // 2. 清空localStorage数据
    window.localStorage.clear();
    // 3. 页面跳转
    this.$router.push('/topiclist/all');
  }
}
</script>


