<template>
  <div>
      <el-card class="box-card">
  <div slot="header" class="clearfix">
    <span>编辑话题</span>
  </div>
  <div>
     <el-input v-model="topic.title" ></el-input>
    <el-input type="textarea" :rows="6"  v-model="topic.content" style="margin:20px 0"></el-input> 
  </div>
  <el-button type="success" @click="edittopic">修改新话题</el-button>
</el-card>
    
  </div>
</template>
<script>
export default {
  name: "edit",
  props: ["id"],
  data: function() {
    return {
      topic: {
        title: "",
        content: ""
      }
    };
  },
  methods: {
    // 修改话题
    edittopic: function() {
      var app = this;
      this.$http
        .post("/topics/update", {
          topic_id: this.topic.id,
          title: this.topic.title,
          content: this.topic.content,
          accesstoken: this.$store.state.accesstoken,
          tab: "dev"
        })
        .then(function(response) {
          // console.log(response.data);
          // 实现页面跳转
          app.$router.back();
        });
    }
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.$http.get("/topic/" + to.params.id).then(function(response) {
        vm.topic = response.data.data;
      });
    });
  },
  beforeRouteUpdate: function(to, from, next) {
    var app = this;
    app.$http.get("/topic/" + to.params.id).then(function(response) {
      app.topic = response.data.data;
    });
    next();
  }
};
</script>


